import type { Metadata } from "next";
// FIX: Use 'localFont' and pass configuration, not named imports
import localFont from "next/font/local"; 
import "./globals.css";

// Define GeistSans using localFont function
const geistSans = localFont({
  src: "./Geist/Geist-Regular.woff2", // Replace with the actual path to your Geist font files
  variable: "--font-geist-sans",
});

// Define GeistMono using localFont function
const geistMono = localFont({
  src: "./Geist/GeistMono-Regular.woff2", // Replace with the actual path to your GeistMono font files
  variable: "--font-geist-mono",
});


export const metadata: Metadata = {
  title: "InRisk Weather Explorer",
  description: "Full-Stack Assessment Solution",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en" suppressHydrationWarning={true}>
      <body
        className={`${geistSans.variable} ${geistMono.variable} antialiased`}
      >
        {children}
      </body>
    </html>
  );
}